@import url(http://fonts.font.im/css?family=Raleway);
body {
  font-family: "Raleway", sans-serif;
  color: "black"; }

#sizeSetting .btn-default.active, #speedSetting .btn-default.active {
  background-color: red;
  color: #fff; }

#sizeSetting {
  margin-bottom: 8px; }

#speedSetting label {
  margin-right: 8px; }

.cell {
  display: inline-block;
  width: 10px;
  height: 10px;
  background-color: #000;
  border-top: 1px solid #333;
  border-left: 1px solid #222; }
  .cell.dead {
    background-color: #000; }
  .cell.alive {
    background-color: #fbb; }
  .cell.old {
    background-color: #e44; }

.line {
  line-height: 1;
  font-size: 0;
  height: 10px;
  overflow: hidden; }

#gameofLife {
  width: 710px; }
  #gameofLife .btn-default {
    margin-right: 10px; }
  #gameofLife #board {
    margin: 5px 0;
    border: 9px solid #333;
    border-radius: 9px;
    -webkit-box-shadow: 0px 16px 30px 0px #200;
            box-shadow: 0px 16px 30px 0px #200;
    background-color: #000; }
  #gameofLife #smallSize {
    margin-left: 10px; }

#generations {
  font-size: 22px;
  color: red;
  padding-left: 80px; }
  #generations label {
    margin-right: 10px;
    color: #000;
    font-weight: normal;
    font-size: 18px; }

#header {
  background-color: #652828;
  font-size: 25px;
  padding: 15px 0; }
  #header a {
    color: #fff; }



